.hubspot {
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.85);
    margin: 2%;
    padding: 10%;
    display: block;
}
nav {
    background-color: rgba(0, 0, 0, 0.75);
    width: 60%;
    margin: auto;
    box-shadow: 7px 8px 8px rgba(0, 0, 0, 0.85);
}

nav hr {
    display: none;
}

a:hover {
    color: rgb(255, 0, 0) !important;
    background: none !important;
}

nav a:focus, .nav .open > a {
    background: none !important;
}

.navbar-toggle, .icon-bar {
    border:1px solid rgb(255, 255, 255) !important;
}

.navbar-left {
    width: 70% !important;
}

.navbar-right {
    width: 30% !important;
}

.dropdown-menu {
    background-color: rgba(0, 0, 0, 0.80) !important;
    border-radius: 7px;
}

.link-mobile {
    display: none !important;
}

@media only screen and (max-width: 767px) {
    nav {
        width: 85%;
    }
    nav {
        text-align: center;
    }
    nav ul {
        margin: 0 !important;
    }
    nav hr {
        display: block;
    }
    nav a {
        line-height: 10px !important;
    }
    .navbar-left, .navbar-right{
        width: 100% !important;
    }
    .link-mobile {
        display: block !important;
    }
    .dropdown {
        display: none !important;
    }
}
.box {
    padding: 2%;
}

table {
    text-align: center;
    width: 100%;
    display: block;
    overflow-x: auto;
}

table td, table th {
    padding: 8px;
    border: 1px solid rgb(255, 255, 255);
}

table th {
    text-align: center;
    color: rgb(255, 255, 255);
}

tbody tr:hover {
    background-color: rgba(255, 255, 255, 0.10);
}



#scrolling-wrapper {
    overflow-y: scroll;
    white-space: nowrap;
}

.msg-list {
    border: 1px solid rgb(255, 255, 255);
    background: rgba(255, 255,255, 0.4);
    border-radius: 20px;
    margin: auto;
    max-height: 450px;
}


/* Top message data */
.msg-data {
    color: rgb(0, 0, 0);
    font-size: 13px;
}

.msg-data-sent {
    text-align: right;
    margin-right: 4%;
}

.msg-data-received {
    text-align: left;
    margin-left: 4%;
}

.msg-data-time {
    margin-left: 3%;
}


/* Message */
.msg {
    margin: 11px;
    font-size: 14px;
    border-radius: 15px;
    padding: 5px;
    position: relative;
}

.msg-sent {
    background-color: rgba(13, 199, 143, 0.45);
    text-align: right;
    padding-right: 20px;
}

.msg-received {
    background-color: rgba(0, 0, 0, 0.4);
    text-align: left;
    padding-left: 20px;
}


/* Message triangle */
.msg:after {
    bottom: 100%;
    content: "";
    position: absolute;
    border: 10px solid transparent;
}

.msg-sent:after {
    right: 5%;
    border-bottom-color: rgba(13, 199, 143, 0.45);
}

.msg-received:after {
    left: 5%;
    border-bottom-color: rgba(0, 0, 0, 0.4);
}


/* Message form (input/button)  */
.msg-field-form {
    width: 60%;
}

.msg-field-form input {
    float:left;
    text-align: left !important;
    width: 86% !important;
    border: none;
}

.msg-field-form button {
    float:right;
    background-color:rgba(13, 199, 143, 0.3);
    width: 12%;
    border: 1px solid rgb(255, 255, 255);   
}

.msg-field-form input, .msg-field-form button {
    border-radius: 15px;
    height: 40px;
}

@media only screen and (max-width: 600px) {
    .msg-field-form, .msg-field-form input {
        width: 100% !important;
    }
    .msg-field-form button {
        width: 30%;
        height: 30px;
        margin-top: 5%;
    }
    .msg-sent:after {
        right: 20%;
    }
    .msg-received:after {
        left: 20%;
    }
    .msg-data-sent {
        margin-right: 20%;
    }
    
    .msg-data-received {
        margin-left: 20%;
    }
}
.collection-folder {
    float: left;
    width: 16%;
    margin-top: 1%;
}

.collection-folder img {
    width: 90px;
    height: 70px;
}

.image-input {
    border-radius: 15px;
    padding: 6px;
    height: 100% !important;
}

.image-container {
    float: left;
    width: 20%;
}

.image-container img {
    width: 80%;
    max-height: 180px;
    min-height: 180px;
    margin-bottom: 20%;
}

.image-container img:hover {
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .collection-folder, .image-container {
        width: 100%;
    }
    .image-container img {
        margin-top: 15%;
        margin-bottom: 0;
    }
}
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.85);
}
  
.modal-content {
    display:inline-block;
    margin: 3% auto;
    border: 2px solid rgb(255, 255, 255);
}
